@import "/Users/maxime/Documents/maxigram/frontend/src/config/_variables.scss";
.comment-box {
    border-top: 1px solid $light-grey;
    padding: 15px 0;
    @include breakpoint("phone") {
        display: none;
    }
    .input {
        border: 0;
        height: 20px;
        resize: 100%;
        width: 100%;
        font-size: $normal-font-size;
        &:focus {
            outline: none;
        }
        &::placeholder {
            color: $light-grey;
        }
    }
}


